<template>
  <div>
    <h3 @click="showUserTimeline(userData.userName, userData.userId)">
      Welcome {{ userData.userName }}
    </h3>

<div>{{notificationCount()}}</div>
<div v-for="notification in notifications">

<h6 @click="showNotificationDetails('friend request',notification.notificationId,notification.userName)" v-if="notification.notificationType === 'friend request' && notification.notificationStatus === 'unRead'&& notification.userName !== userData.userName">{{notification.userName}}  sent you a {{notification.notificationType}}</h6>
<h6   @click="showNotificationDetails('message',notification.notificationId,notification.userName)" v-if="notification.notificationType === 'message' && notification.notificationStatus === 'unRead'&& notification.userName !== userData.userName">You have a new {{notification.notificationType}} from {{notification.userName}}</h6>
<h6   @click="showNotificationDetails('post',notification.notificationId,notification.userName)" v-if="notification.notificationType === 'post' && notification.notificationStatus === 'unRead'&& notification.userName !== userData.userName">Your friend {{notification.userName}} just made a new {{notification.notificationType}}</h6>
<h6   @click="showNotificationDetails('likes',notification.notificationId,notification.userName)" v-if="notification.notificationType === 'likes' && notification.notificationStatus === 'unRead'&& notification.userName === userData.userName">{{notification.userName}} likes your post</h6>
<h6   @click="showNotificationDetails('comment',notification.notificationId,notification.userName)" v-if="notification.notificationType === 'comment' && notification.notificationStatus === 'unRead'&& notification.userName === userData.userName">{{notification.userName}} commented on your post</h6>
<h6   @click="showNotificationDetails('comment',notification.notificationId,notification.userName,notification.posterUserName)" v-if="notification.notificationType === 'comment' && notification.notificationStatus === 'unRead'&& notification.userName !== userData.userName">Your friend {{notification.userName}} commented on {{notification.posterUserName}}'s post</h6>
<h6   @click="showNotificationDetails('likes',notification.notificationId,notification.userName,notification.posterUserName)" v-if="notification.notificationType === 'likes' && notification.notificationStatus === 'unRead'&& notification.userName !== userData.userName">Your friend {{notification.userName}} likes {{notification.posterUserName}}'s post</h6>
<h6   @click="showNotificationDetails('follow',notification.notificationId,notification.userName)" v-if="notification.notificationType === 'follow' && notification.notificationStatus === 'unRead' && notification.userName !== userData.userName">{{notification.userName}} started following you {{notification.posterUserName}}'s post</h6>

</div>

    <div class="container">
      <div class="container1">
        <ul class="list-group">
          <li class="list-group-item bg-info" @click="showMessages">
            Messages
          </li>
          <li class="list-group-item bg-info" @click="showFriends">
            Friends
            <span>{{ userData.friends.length }}</span>
          </li>
          <li class="list-group-item bg-info" @click="showTimeline">
            My Timeline
          </li>
          <li class="list-group-item bg-info" @click="showFollowing">
            Following <span>{{ userData.following.length }}</span>
          </li>
          <li class="list-group-item bg-info" @click="showFollowers">
            Followers
            <span>{{ userData.followers.length }}</span>
          </li>
        </ul>
      </div>
      <div class="container2">
        <form @submit.prevent="handlePublishPost()" class="m-5">
          <textarea
            name="posttextarea"
            v-model="postTextArea"
            cols="30"
            rows="3"
            placeholder="make a post"
          ></textarea>

          <br />

          <button type="submit" class="btn btn-success">Post</button>
        </form>

        <div v-for="post in newsFeeds" :key="post.postId">
       
            <h5
              class="bg-success"
              @click="showUserTimeline(post.userName, post.userId)"
            >
              {{ post.userName }}
            </h5>
      
          <div class="posted m-5 rounded">
            {{ post.posts }}
          </div>
          <p>{{ showDate(post.datePosted) }}</p>
          <span
            @click="
              handleLikes(
                'postLikes',
                'incrementPostLikes',
                post.postId,
                userData.userId,
post.userName
              )
            "
            class="m-2 p-5 text-success"
            ><i class="fa fa-thumbs-up"></i>{{ post.likes.length }}</span
          >
          <span
            @click="
              handleLikes(
                'postLikes',
                'decrementPostLikes',
                post.postId,
                userData.userId,
post.userName
              )
            "
            class="m-2 p-5"
            ><i class="fa fa-thumbs-down text-danger"></i
            >{{ post.unLikes.length }}</span
          >
          <div v-for="comment in post.comments" :key="comment.commentId">
   <!-- <router-link :to="{name:'Timeline',params:{userName:comment.userName,Timeline:'Timeline'}}" >  </router-link>      -->
              <span
                @click="showUserTimeline(comment.userName, comment.userId)"
                >{{ comment.userName }}</span
              >
          
            <p>{{ comment.comment }}</p>
 <p>{{ showDate(comment.dateCommented) }}</p>
            <span
              @click="
                handleLikes(
                  'commentLikes',
                  'incrementCommentLikes',
                  post.postId,
                  userData.userId,
                  comment.commentId,
post.userName
                )
              "
              class="m-2 p-5 text-success"
              ><i class="fa fa-thumbs-up"></i>{{ comment.likes.length }}</span
            >
            <span
              @click="
                handleLikes(
                  'commentLikes',
                  'decrementCommentLikes',
                  post.postId,
                  userData.userId,
                  comment.commentId,
post.userName
                )
              "
              class="m-2 p-5"
              ><i class="fa fa-thumbs-down text-danger"></i
              >{{ comment.unLikes.length }}</span
            >
          </div>

          <form
            @submit.prevent="
              handlePosterComment(post.posterComment, post.postId,post.userName)
            "
            class="m-5"
          >
            <input
              type="text"
              placeholder="Post a comment"
              v-model="post.posterComment"
            />

            <button type="submit">Comment</button>
          </form>
        </div>
      </div>
      <div class="container3"></div>
    </div>
  </div>

  <h1>{{ comments }}</h1>
</template>


<script>
import LogInPage from "./LogInPage";
import { uuid } from "vue-uuid";
import moment from 'moment'

export default {
  name: "Home",
  components: { LogInPage },
  props: ["comments"],
  data() {
    return {
      userData: {
        userName: "Guest",
        userId: "419",
        emailAddress: "419",
        firstName: "",
        lastName: "",
        address: "",
        emailAddress: "",
        postCode: "",
        country: "",
        city: "",
        aboutMe: "",
        password: "",
        messages: [],
        posts: [],
        followers: [],
        following: [],
        friends: [],
        likes: [],
        unLikes: [],
        posterComment: "",
        comments: {
          likes: [],
          unLikes: [],
        },
      },

      newsFeed: {},
      postTextArea: "",
      displayFriends: false,
      displayFollowers: false,
      displayFollowing: false,
      notifications:[]
    };
  },

  mounted() {
    this.loadData();
  },
  beforeUnmount() {
    this.loadData();
  },

  methods: {
    loadData() {
      this.newsFeed = this.$store.state.newsFeed;
      this.userData = this.$store.state.userData;

let notifications = []
for(const notificationId in this.userData.notifications ){notifications = [...notifications,this.userData.notifications[notificationId]]
}
this.notifications = notifications

    },



    showMessages() {
      if (this.userData.userName !== "Guest") {
        this.$router.push({
          name: "Messages",
          params: { userName: this.userData.userName },
        });
      }
    },
    showFriends() {
      if (this.userData.userName !== "Guest") {
        this.displayFriends = !this.displayFriends;
        this.$router.push({
          name: "Friends",
          params: { userName: this.userData.userName },
        });
      }
    },
    showTimeline() {
      if (this.userData.userName !== "Guest") {
      }
    },
    showFollowers() {
      if (this.userData.userName !== "Guest") {
        this.displayFollowers = !this.displayFollowers;
      }
    },
    showFollowing() {
      if (this.userData.userName !== "Guest") {
        this.displayFollowing = !this.displayFollowing;
      }
    },

    showUserTimeline(userName) {   
        this.$router.push({
          name: "Timeline",
          params: { userName:userName,Timeline:"Timeline" },
        });

    },

    handlePublishPost() {
      let postId = uuid.v1();

      if (this.postTextArea.length) {
        this.$store.dispatch("handlePublishPost", {
          [postId]: {
            userName: this.userData.userName,
            userId: this.userData.userId,
            postId,
            datePosted: Date.now(),
            posts: this.postTextArea,
            likes: [],
            unLikes: [],
            posterComment: "",
            comments: [],
          },
        });
        this.postTextArea = "";
        this.loadData();
      }

 this.$store.dispatch("handleNotifications", {
       userName:this.userData.userName,
        notificationId: uuid.v1(),
       notificationType: 'post',
notificationStatus:"unRead",
notificationDate: Date.now(),
      });


    },

    handleLikes(condition, params, postId, userId, commentId,posterUserName) {
      this.$store.dispatch("handleLikes", {
        condition,
        params,
        postId,
        userId,
        commentId,
        dateLiked: Date.now(),
      });

 this.$store.dispatch("handleNotifications", {
       userName:this.userData.userName,
      posterUserName,
        notificationId: uuid.v1(),
       notificationType:'likes',
notificationStatus:"unRead",
notificationDate: Date.now(),
      });

    },

    handlePosterComment(comment, postId,posterUserName) {
      let commentId = uuid.v1();
      if (comment.length) {
        this.$store.dispatch("handlePosterComment", {
          [commentId]: {
            userName: this.userData.userName,
            userId: this.userData.userId,
            comment,
            dateCommented: Date.now(),
            commentId,
            likes: [],
            postId,
            unLikes: [],
          },
        });

 this.$store.dispatch("handleNotifications", {
       userName:this.userData.userName,
      posterUserName,
        notificationId: uuid.v1(),
       notificationType:'comment',
notificationStatus:"unRead",
notificationDate: Date.now(),
      });


      }
    },
  

  showDate(date) {
    const currentDate = Date.now();
    const dateStatus = currentDate - date;
    const minutes = Math.round(dateStatus / (1000 * 60));
    const hours = Math.round(dateStatus / (1000 * 60 * 60));
    const days = Math.round(dateStatus / (1000 * 60 * 60 * 24));
    const weeks = Math.round(dateStatus / (1000 * 60 * 60 * 24 * 7));
    const months = Math.round(dateStatus / (1000 * 60 * 60 * 24 * 7 * 12));
    const years = Math.round(dateStatus / (1000 * 60 * 60 * 365));

    if (minutes <= 0) {
      return "Just now";
    } else if (minutes < 59) {
      return minutes === 1 ? "1 minute ago" : minutes + " minutes ago";
    } else if (hours < 11) {
      return hours === 1 ? "1 hour ago" : hours + " hours ago";
    } else if (days >= 1) {
      return days === 1 ? "1 day ago" : days + " days ago";
    } else if (weeks < 59) {
     return weeks === 1 ? "1 week ago" : weeks + " weeks ago";
    } else if (months < 59) {
       return    months === 1 ? "1 month ago" : months + " mouths ago";
    } else {
      return   years === 1 ? "1 year ago" : years + " years ago";
    }
  },


// toggleNotificationDisplay(notificationType,notificationStatus){
// if (notificationType === "friend request" && notificationStatus === 'unRead' ) {
//   return true
// }
// else if(notificationType === "message" && notificationStatus === 'unRead'){
// return true


// }
// else if(notificationType === "post" && notificationStatus === 'unRead'){

// return true

// }

// else if(notificationType === "likes" && notificationStatus === 'unRead'){
// return true


// }

// else if(notificationType === "likes" && notificationStatus === 'unRead'){

// return true

// }
// else{
// return false
// }

// },


notificationCount(){
const notificationCount = this.notifications.filter((noticeStatus)=>noticeStatus.notificationStatus === "unRead")

return notificationCount.length
},




showNotificationDetails(notificationType,notificationId,userName,posterUserName){

switch (notificationType) {

  case 'friend request':
     this.$router.push({
          name: "Friends",
          params: { userName: this.userData.userName },
        })
    break;


 case 'message':
 this.$store.dispatch("handleMessageStatus", {
        friendMessaged: userName,
        userName: this.userData.userName,
        userMessageStatus: "Read",
        friendMessageStatus: "Read",
        showMessage:"on"
      });


     this.$router.push({
          name: "Messages",
          params: { userName: this.userData.userName },
        })
    break;

 case 'post':
     this.$router.push({
          name: "Timeline",
          params: {userName,Timeline:"Timeline" },
        })



    break;

 case 'likes':
     this.$router.push({
          name: "Timeline",
          params: { userName: posterUserName,Timeline:"Timeline" },
        })

 this.$store.dispatch("handleNotificationUpdate", {
        userName: this.userData.userName,
notificationStatus:"read",
notificationType:'likes'
      });
    break;

case 'comment':
     this.$router.push({
          name: "Timeline",
          params: { userName: posterUserName,Timeline:"Timeline" },
        })

 this.$store.dispatch("handleNotificationUpdate", {
        userName: this.userData.userName,
notificationStatus:"read",
notificationType:'comment'
      });
    break;

  default:
    break;
}



}


  },
  computed: {
    newsFeeds() {
let newsFeeds = []

for (const newsFeed in this.newsFeed){

newsFeeds.push(this.newsFeed[newsFeed])

}





      return newsFeeds.sort((a, b) => a.datePosted - b.datePosted ).reverse();
    },
  },
};
</script>

