<template>

<!-- <h1>   {{$store.state.users[userName]}} </h1> -->

 <form @submit.prevent="handlePublishPost()" class="m-5">
          <textarea
            name="posttextarea"
            v-model="postTextArea"
            cols="30"
            rows="3"
            placeholder="make a post"
          ></textarea>

          <br />

          <button type="submit" class="btn btn-success">Post</button>
        </form>
<div v-for="post in userDatas.posts" :key="post.id">


            <h5
              class="bg-success"
              @click="showUserTimeline(post.userName, post.userId)"
            >
              {{ post.userName }}
            </h5>
         
          <div class="posted m-5 rounded">
            {{ post.posts }}
          </div>
          <p>{{ showDate(post.datePosted) }}</p>
          <span
            @click="
              handleLikes(
                'postLikes',
                'incrementPostLikes',
                post.postId,
                userData.userId
              )
            "
            class="m-2 p-5 text-success"
            ><i class="fa fa-thumbs-up"></i>{{ post.likes.length }}</span
          >
          <span
            @click="
              handleLikes(
                'postLikes',
                'decrementPostLikes',
                post.postId,
                userData.userId
              )
            "
            class="m-2 p-5"
            ><i class="fa fa-thumbs-down text-danger"></i
            >{{ post.unLikes.length }}</span
          >






  <div v-for="comment in post.comments" :key="comment.commentId">
           
              <span
                @click="showUserTimeline(comment.userName, comment.userId)"
                >{{ comment.userName }}</span
              >
           
            <p>{{ comment.comment }}</p>
 <p>{{ showDate(comment.dateCommented) }}</p>
            <span
              @click="
                handleLikes(
                  'commentLikes',
                  'incrementCommentLikes',
                  post.postId,
                  userData.userId,
                  comment.commentId
                )
              "
              class="m-2 p-5 text-success"
              ><i class="fa fa-thumbs-up"></i>{{ comment.likes.length }}</span
            >
            <span
              @click="
                handleLikes(
                  'commentLikes',
                  'decrementCommentLikes',
                  post.postId,
                  userData.userId,
                  comment.commentId
                )
              "
              class="m-2 p-5"
              ><i class="fa fa-thumbs-down text-danger"></i
              >{{ comment.unLikes.length }}</span
            >
 
          </div>
 <form
            @submit.prevent="
              handlePosterComment(post.posterComment, post.postId)
            "
            class="m-5"
          >
            <input
              type="text"
              placeholder="Post a comment"
              v-model="post.posterComment"
            />

            <button type="submit">Comment</button>
          </form>
</div>
</template>

<script>
import { uuid } from "vue-uuid";



export default {
  name: 'Posts',
props:['userName','loadData'],

data() {
  return {
  userData:{},
    postTextArea: "",
  }
},
mounted(){
this.loadUserData() 


},
beforeUnmount(){
this.loadUserData() 
},



methods: {

    loadUserData() {
      this.userData = this.$store.state.userData;
    },

 handleLikes(condition, params, postId, userId, commentId) {
      this.$store.dispatch("handleLikes", {
        condition,
        params,
        postId,
        userId,
        commentId,
        dateLiked: Date.now(),
      });
//  this.loadData() 
    },
  showDate(date) {
    const currentDate = Date.now();
    const dateStatus = currentDate - date;
    const minutes = Math.round(dateStatus / (1000 * 60));
    const hours = Math.round(dateStatus / (1000 * 60 * 60));
    const days = Math.round(dateStatus / (1000 * 60 * 60 * 24));
    const weeks = Math.round(dateStatus / (1000 * 60 * 60 * 24 * 7));
    const months = Math.round(dateStatus / (1000 * 60 * 60 * 24 * 7 * 12));
    const years = Math.round(dateStatus / (1000 * 60 * 60 * 365));

    if (minutes <= 0) {
      return "Just now";
    } else if (minutes < 59) {
      return minutes === 1 ? "1 minute ago" : minutes + " minutes ago";
    } else if (hours < 11) {
      return hours === 1 ? "1 hour ago" : hours + " hours ago";
    } else if (days >= 1) {
      return days === 1 ? "1 day ago" : days + " days ago";
    } else if (weeks < 59) {
     return weeks === 1 ? "1 week ago" : weeks + " weeks ago";
    } else if (months < 59) {
       return    months === 1 ? "1 month ago" : months + " mouths ago";
    } else {
      return   years === 1 ? "1 year ago" : years + " years ago";
    }
  },

 handlePosterComment(comment, postId) {
      let commentId = uuid.v1();
      if (comment.length) {
        this.$store.dispatch("handlePosterComment", {
          [commentId]: {
            userName: this.userData.userName,
            userId: this.userData.userId,
            comment,
            dateCommented: Date.now(),
            commentId,
            likes: [],
            postId,
            unLikes: [],
          },
        });
      }
    },
    showUserTimeline(userName) {

        this.$router.push({
          name: "Timeline",
          params: { userName,Timeline:"Timeline" },
        });
 this.loadData(userName) 
// this.$emit("sendUserName", this.userName)
    },



    handlePublishPost() {
      let postId = uuid.v1();

      if (this.postTextArea.length) {
        this.$store.dispatch("handlePublishPost", {
          [postId]: {
            userName: this.userData.userName,
            userId: this.userData.userId,
            postId,
            datePosted: Date.now(),
            posts: this.postTextArea,
            likes: [],
            unLikes: [],
            posterComment: "",
            comments: [],
          },
        });
        this.postTextArea = "";
        this.loadData(this.userData.userName);
      }
    },
  


},
computed:{
userDatas(){
let userData = this.$store.state.users[this.userName] 
for(let userName in this.$store.state.newsFeed){
if(this.$store.state.newsFeed[userName].userName === userData.userName){

userData.posts = [...userData.posts,this.$store.state.newsFeed[userName]].sort((a, b) => a.datePosted - b.datePosted ).reverse();

}

}

return userData

}


}




}







</script>

