<template>
  
       <div class="card card-profile shadow mt-5">
            <div class="row justify-content-center">
              <div class="col-lg-3 order-lg-2">
                <div class="card-profile-image">
                  <a href="#">
                    <img src="https://demos.creative-tim.com/argon-dashboard/assets/img/theme/team-4.jpg" class="rounded-circle">
                  </a>
                </div>
              </div>
            </div>
            <div class="card-header text-center border-0 pt-8 pt-md-4 pb-0 pb-md-4">
              <div class="d-flex justify-content-between">
                <a href="#" class="btn btn-sm btn-info mr-4" @click="handleFriendRequest( $store.state.allUsers[userName],'Add Friend')"> {{ $store.state.allUsers[userName].requestStatus }}</a>
                <a href="#" class="btn btn-sm btn-default btn-success float-right" @click="showMessage(userName, 'Read', 'Read')">Message</a>
              </div>
            </div>
            <div class="card-body pt-0 pt-md-4">
              <div class="row">
                <div class="col">
                  <div class="card-profile-stats d-flex justify-content-center mt-md-5">
                    <div>
                      <span class="heading">{{userData.friends.length}}</span>
                      <span class="description" @click="$emit('toggleView','displayFriends')">
<router-link :to="{name:'Timeline',params:{userName:userData.userName,Timeline:'Friends'}}">Friends</router-link>
</span>
                    </div>
                    <div>
                      <span class="heading">10</span>
                      <span class="description">Photos</span>
                    </div>
                    <div>
                      <span class="heading">{{userData.posts.length}}</span>

                      <span class="description" @click="$emit('toggleView','displayPosts')">
<router-link :to="{name:'Timeline',params:{userName:userData.userName,Timeline:'Friends'}}">Posts</router-link></span>
                    </div>
                  </div>
                </div>
              </div>
              <div class="text-center">
                <h3>
              {{userNames}} {{userData.lastName}}<span class="font-weight-light">, 27</span>
                </h3>
                <div class="h5 font-weight-300">
                  <i class="ni location_pin mr-2"></i>Bucharest, biafra
                </div>
                <div class="h5 mt-4">
                  <i class="ni business_briefcase-24 mr-2"></i>Solution Manager - Creative Tim Officer
                </div>
                <div>
                  <i class="ni education_hat mr-2"></i>University of Computer Science
                </div>
                <hr class="my-4">
                <p>Ryan  the name taken by Melbourne-raised, Brooklyn-based Nick Murphy  writes, performs and records all of his own music.</p>
            
              </div>
            </div>
          </div>

</template>
<script>
export default {
    name:"Friends",
props:["userName"],
data() {
    return {
   userData: {
            userName: "Guest",
            userId: "419",
            emailAddress: "419",
            firstName: "Guest",
            lastName: "Guest",
            address: "Guest",
            emailAddress: "Guest@gmail.com",
            postCode: "Guest",
            country: "Guest",
            city: "Guest",
            aboutMe: "Guest",
            password: "Guest",
            occupation: "Guest",
            education: "Guest",
            age: "27",

            status: 'offline',
            messages: [],
            posts: {
                Guest: {
                    likes: ["Guest"],
                    unLikes: ["Guest"],
                }
            },
            followers: ["Guest"],
            following: ["Guest"],
            friends: ["Guest"],

            posterComment: "",
            comments: {
                likes: [],
                unLikes: [],
                notifications: {
                    friendRequest: [],

                }


            }
        },    }
},
mounted(){
this.loadData()
},
beforeUnmount(){

this.loadData()

},


methods:{
loadData(){

this.userData = this.$store.state.users[this.userName]
for(const userName in this.$store.state.newsFeed){
if(this.$store.state.newsFeed[userName].userName === this.userData.userName){

this.userData.posts = [...this.userData.posts,this.$store.state.newsFeed[userName]]

}

}



},



showMessage(userName, status1, status2){
 
      if (this.userData.userName !== "Guest") {
   this.$store.dispatch("handleMessageStatus", {
        friendMessaged: userName,
        userName: this.userData.userName,
        userMessageStatus: status1,
        friendMessageStatus: status2,
        showMessage:"on"
      });
  
this.$router.push({
          name: "Messages",
          params: { userName: this.userData.userName },
        })


        
      }

   


},





    handleFriendRequest(user,requestStatus) {

switch (requestStatus) {
  case "Add Friend":
    
   user.requestStatus ="Friend Request Sent";

      this.$store.dispatch("handleFriendRequest", {
        friendUserName: user.userName,
        userName: this.userData.userName,
        requestStatus: "Friend Request Sent",
      });
    break;

  case "Accept Friend Request":
  this.$store.dispatch("handleFriendRequest", {
        friendUserName: user.userName,
        userName: this.userData.userName,
        requestStatus: "Accept Friend Request",
      });



  break;

  default:
    break;
}

    }

},
computed:{
userNames(){
let userName = this.userName

if (this.userData.userName!== this.userName)
{this.loadData()}

return userName

}

 }



}
</script>