<template>
  <div class="message-containner">
    <div class="containerlist bg-success">
      <ul
        v-for="userName in messagedUsersList"
        :key="userName"
        @click="showMessage(userName, 'Read', 'Read')"
      >
        <li>
          {{ userName }}
          <span>
            {{ userData.messageStatus[userName].friendMessageStatus }}
          </span>
        </li>
      </ul>
    </div>
    <div class="conmessage bg-info">
      <div v-for="message in showingMessages" :key="message.messageId">
        <!-- <p>  {{moment().format('LT')}} </p> -->
        <p>{{ showDate(message.messageDate) }}</p>
        <span>{{ message.userName }}</span>
        <p>
          {{ message.message }}
          <span>{{
            showMessageStatuses(message.messageStatus, message.messageId)
          }}</span>
        </p>

        <input
          type="text"
          value="delete message"
          class="btn btn-danger"
          @click="
            handleDeleteMessage(
              userData.userName,
              friendMessaged,
              message.messageId
            )
          "
        />
      </div>

      <form @submit.prevent="handleSubmitMessage">
        <input type="text" v-model="message" placeholder="Type your message" />

        <button type="submit" class="btn btn-success">send</button>
      </form>
    </div>

    <div class="containerlist bg-success">
      <ul v-for="friend in userData.friends" :key="friend.userName">
        <li @click="showMessage(friend.userName)">
          {{ friend.userName }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { uuid } from "vue-uuid";
import moment from "moment";

export default {
  name: "Messages",
  props: ["userName"],

  data() {
    return {
      userData: {},
      showingMessage: [],
      friendMessaged: "",
      messagedUsersList: [],
      message: "",
showingMessageStatus:[],
    };
  },

  mounted() {
    this.loadData();
  },
  beforeUnmount() {
    this.loadData();
  },
  methods: {
    loadData() {
      this.userData = this.$store.state.users[this.userName];

      this.messagedUsersList = Object.keys(this.userData.messages);

  let messageStatus = [];

      for (const message in  this.userData.messageStatus) {
        messageStatus.push(this.userData.messageStatus[message]);
      }

this.showingMessageStatus = messageStatus.filter((message)=>message.showMessage === 'on');

  this.showingMessage = this.userData.messages[this.showingMessageStatus[0].friendMessaged];

      this.friendMessaged =this.showingMessageStatus[0].friendMessaged

      this.showMessage(this.friendMessaged, "unRead", "Read");

 this.$store.dispatch("handleNotificationUpdate", {
        userName: this.userData.userName,
notificationStatus:"read",
notificationType:'message'
      });




    },

    handleSubmitMessage() {
      const messageId = uuid.v1();
if(this.message && this.friendMessaged){
      this.$store.dispatch("handleSubmitMessage", {
        [messageId]: {
          userName: this.userData.userName,
          userId: this.userData.userId,
          message: this.message,
          messageId,
          friendMessaged: this.friendMessaged,
          messageDate: Date.now(),
          messageStatus: "sent",
        },
      });
      this.showMessage(this.friendMessaged, "unRead", "Read");

this.$store.dispatch("handleNotifications", {
       userName:this.userData.userName,
 friendUserName:this.friendMessaged,
        notificationId: uuid.v1(),
       notificationType: 'message',
notificationStatus:"unRead",
notificationDate: Date.now()
      });

      this.message = "";
      this.showingMessage = this.userData.messages[this.friendMessaged];
      this.messagedUsersList = Object.keys(this.userData.messages);
}
    },

    showMessage(userName, status1, status2) {
      this.showingMessage = this.userData.messages[userName];
      this.friendMessaged = userName;

      this.$store.dispatch("handleMessageStatus", {
        friendMessaged: userName,
        userName: this.userData.userName,
        userMessageStatus: status1,
        friendMessageStatus: status2,
        showMessage: "on",
      });
    },

    showMessageStatuses(messageStatus, messageId) {

      this.$store.dispatch("showMessageStatuses", {
        friendMessaged: this.friendMessaged,
        userName: this.userData.userName,
      });

      if (messageStatus === "sent") {
        return "1";
      } else if (messageStatus === "received") {
        return "11";
      } else {
        return "read";
      }
    },

    showDate(messageDate) {
      let currentDate = Date.now();
      let dateStatus = currentDate - messageDate;
      const minutes = Math.round(dateStatus / (1000 * 60));
      const hours = Math.round(dateStatus / (1000 * 60 * 60));
      const days = Math.round(dateStatus / (1000 * 60 * 60 * 24));
      const weeks = Math.round(dateStatus / (1000 * 60 * 60 * 24 * 7));
      const months = Math.round(dateStatus / (1000 * 60 * 60 * 24 * 7 * 12));
      const years = Math.round(dateStatus / (1000 * 60 * 60 * 365));

      if (minutes <= 0) {
        return "Just now";
      } else if (minutes < 59) {
        return minutes === 1 ? "1 minute ago" : minutes + "minutes ago";
      } else if (hours < 11) {
        return moment(messageDate).format("L, h:mm :ss a");
      } else if (days >= 1) {
        return moment(messageDate).format("L, h:mm:ss a");
      } else if (weeks < 59) {
        return moment(messageDate).format("L, h:mm :ss a");
      } else if (months < 59) {
        return moment(messageDate).format("L, h:mm :SS a");
      } else {
        return moment(messageDate).format("L, h:mm a");
      }
    },

    handleDeleteMessage(userName, friendMessaged, messageId) {
      this.$store.dispatch("handleDeleteMessage", {
        userName,
        friendMessaged,
        messageId,
      });
    },
  },
  computed: {
    showingMessages() {
      let showingMessages = [];

      for (const message in this.showingMessage) {
        showingMessages.push(this.showingMessage[message]);
      }
      return showingMessages.sort((a, b) => a.messageDate - b.messageDate);
    },
  },
};
</script>

<style scoped>
.message-containner {
  display: flex;
  justify-content: center;
  flex-flow: row nowrap;
}
.containerlist {
  width: 20%;
  height: 100vh;
  padding-top: 30px;
}
.conmessage {
  width: 60%;
  height: 100vh;
  padding-top: 30px;
}
</style>