<template>
  <div class="container">
    <div class="container1">
      <ul class="list-group">
        <li class="list-group-item bg-info" @click="showFriends">
          Friends
          <span>{{ userData.friends.length }}</span>
        </li>

        <li class="list-group-item bg-info" @click="showFollowing">
          Following <span>{{ userData.following.length }}</span>
        </li>
        <li class="list-group-item bg-info" @click="showFollowers">
          Followers
          <span>{{ userData.followers.length }}</span>
        </li>
      </ul>
    </div>

    <div class="container3">
      <ul
        v-show="displayFriends"
        v-for="user in userData.friends"
        :key="user.userName"
      >
        <router-link
          :to="{ name: 'userProfile', params: { userName: user.userName } }"
        >
          <li @click="showUserProfile(user.userName, user.userId)">
            {{ user.userName }}
          </li>
        </router-link>
      </ul>

      <ul
        v-show="displayFollowers"
        v-for="user in userData.followers"
        :key="user.userName"
      >
        <router-link
          :to="{ name: 'userProfile', params: { userName: user.userName } }"
        >
          <li @click="showUserProfile(user.userName, user.userId)">
            {{ user.userName }}
          </li>
        </router-link>
      </ul>

      <ul
        v-show="displayFollowing"
        v-for="user in userData.following"
        :key="user.userName"
      >
        <router-link
          :to="{ name: 'userProfile', params: { userName: user.userName } }"
        >
          <li @click="showUserProfile(user.userName, user.userId)">
            {{ user.userName }}
          </li>
        </router-link>
      </ul>
    </div>
    <div class="container2">
      <h4>People you May know</h4>

      <ul v-for="user in randomUsers" :key="user.userName">
        <li @click="showUserProfile(user.userName)">{{ user.userName }}</li>
        <button class="btn btn-info" @click="handleFriendRequest(user,user.requestStatus)">
          {{ user.requestStatus }}
        </button>

  <button class="btn btn-info" v-if="user.requestStatus === 'Friend Request Sent'" @click="handleCancelFriendRequest(user, user.userName )">
          Cancel request
        </button>
        <button class="btn btn-info"  v-if="!followingList.includes(user.userName)" @click="handleFollow(user.userName,'follow')">follow</button>
        <button class="btn btn-info"  v-if="followingList.includes(user.userName)" @click="handleFollow(user.userName,'unfollow')">unfollow</button>

      </ul>
    </div>
  </div>
</template>

<script>
import { uuid } from 'vue-uuid';



export default {
  name: "Friends",
  props: ["userName"],

  data() {
    return {
      userData: {
        userName: "Guest",
        userId: "419",
        emailAddress: "419",
        firstName: "Guest",
        lastName: "Guest",
        address: "Guest",
        emailAddress: "Guest@gmail.com",
        postCode: "Guest",
        country: "Guest",
        city: "Guest",
        aboutMe: "Guest",
        password: "Guest",
        occupation: "Guest",
        education: "Guest",
        age: "27",
        messages: [],
        posts: {
          Guest: { likes: ["Guest"], unLikes: ["Guest"] },
        },
        followers: ["Guest"],
        following: ["Guest"],
        friends: ["Guest"],

        posterComment: "",
        comments: {
          likes: [],
          unLikes: [],
        },
      },
      allUsers: [],
      displayFriends: true,
      displayFollowers: false,
      displayFollowing: false,
followingList:[]
    };
  },
  mounted() {
    this.loadData();
  },
  beforeUnmount() {
    this.loadData();
  },
  methods: {
    loadData() {
      this.userData = this.$store.state.users[this.userName];

const followingList = this.userData.following
this.followingList = followingList.map((userName)=> userName.userName)


 this.$store.dispatch("handleNotificationUpdate", {
        userName: this.userData.userName,
notificationStatus:"read",
notificationType:'friend request'
      });

      const users = Object.keys(this.$store.state.allUsers);
      const allUsers = this.$store.state.allUsers;



// for (let user in allUsers){

// if(user !== this.userData.userName){
//         for (let index in allUsers[user].requests) {
// if (allUsers[user].requests[index].userName === this.userData.userName)
// {
// allUsers[user].requestStatus = "Friend Request Sent";
// }
// else {
// allUsers[user].requestStatus = "Add Friend";

// }

//         }


//         }
// else{
//         for (let index in allUsers[user].requests) {
// if(users.includes(allUsers[user].requests[index].userName))

// {

// allUsers[user].requestStatus = "Accept Friend Request";
// }
// else{

// allUsers[user].requestStatus = "Add Friend";

// }
// }

// }
// }

// this.allUsers = allUsers;

const friendsList = this.userData.friends.map((user)=>user.userName) 

      users.forEach((user) => {
        for (let index in allUsers[user].requests) {
if(!friendsList.includes(user)){
 allUsers[user].requestStatus = "Add Friend";

}
         else if (
            allUsers[user].requests[index].userName === this.userData.userName
          ) {
            allUsers[user].requestStatus = "Friend Request Sent";
          }
         else if (allUsers[this.userData.userName].requests.length) {
            allUsers[
              allUsers[this.userData.userName].requests[index].userName
            ].requestStatus = "Accept Friend Request";
          }

          this.allUsers = [...this.allUsers, this.$store.state.allUsers[user]];
        }
      });
   
    },

    toggleDisplay(display) {
      switch (display) {
        case "displayFriends":
          this.displayFriends = true;
          this.displayFollowers = false;
          this.displayFollowing = false;

          break;

        case "displayFollowers":
          this.displayFriends = false;
          this.displayFollowers = true;
          this.displayFollowing = false;
          break;

        case "displayFollowing":
          this.displayFriends = false;
          this.displayFollowers = false;
          this.displayFollowing = true;
          break;

        default:
          break;
      }
    },
    showFriends() {
      if (this.userData.userName !== "Guest") {
        this.toggleDisplay("displayFriends");
        this.$router.push({
          name: "Friends",
          params: { userName: this.userData.userName },
        });
      }
    },
    showFollowers() {
      if (this.userData.userName !== "Guest") {
        this.toggleDisplay("displayFollowers");
      }
    },
    showFollowing() {
      if (this.userData.userName !== "Guest") {
        this.toggleDisplay("displayFollowing");
      }
    },


  showUserProfile(userName) {
      if (this.userData.userName !== "Guest") {
        this.$router.push({
          name: "userProfile",
          params: { userName: this.userData.userName },
        });
      }
    },


handleFollow(userName,followState){
  this.$store.dispatch("handleFollow", {
        friendUserName:userName,
        userName: this.userData.userName,
followState
      });

let followingList = this.userData.following

this.followingList = followingList.map((userName)=> userName.userName)


},



    handleFriendRequest(user,requestStatus) {
switch (requestStatus) {
  case "Add Friend":
    
   user.requestStatus= "Friend Request Sent";

      this.$store.dispatch("handleFriendRequest", {
        friendUserName: user.userName,
        userName: this.userData.userName,
        requestStatus: "Friend Request Sent",
      });

 this.$store.dispatch("handleNotifications", {
       userName:this.userData.userName,
 friendUserName:user.userName,
        notificationId: uuid.v1(),
       notificationType: 'friend request',
notificationStatus:"unRead",
notificationDate: Date.now(),

      });

    break;

  case "Accept Friend Request":
  this.$store.dispatch("handleFriendRequest", {
        friendUserName: user.userName,
        userName: this.userData.userName,
        requestStatus: "Accept Friend Request",
      });



  break;

  default:
    break;
}

    },

handleCancelFriendRequest(user,friendUserName){


  this.$store.dispatch("handleCancelFriendRequest",{
userName:friendUserName,
friendUserName:this.userData.userName
}
  )
this.loadData()


},

  },

  computed: {
    randomUsers() {
      const friends = this.userData.friends.map((friend) => friend.userName);
let allUsers = []
for(const user in this.$store.state.allUsers){
allUsers = [...allUsers,this.$store.state.allUsers[user] ]

}



      const randomUsers = allUsers
        .filter(
          (user) =>
            [this.userData.userName, ...friends].indexOf(user.userName) < 0
        )
        .map((a) => ({ sort: Math.random(), value: a }))
        .sort((a, b) => a.sort - b.sort)
        .map((a) => a.value);


      return randomUsers;
    },
  },
};
</script>




















<style scoped>
.container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
}
.container1 {
  background-color: chartreuse;
  width: 20%;
}
.container2 {
  background-color: chartreuse;
  width: 20%;
}
.container3 {
  background-color: rgb(30, 15, 167);
  width: 60%;
}
</style>